class RevisionHistoryIntegration {
	constructor( editor ) {
		this.editor = editor;
	}

	static get pluginName() {
		return 'RevisionHistoryIntegration';
	}

	static get requires() {
		return [ 'RevisionHistory' ];
	}

	init() {
		const revisionHistory = this.editor.plugins.get( 'RevisionHistory' );

		for ( const revisionData of appData.revisions ) {
			revisionHistory.addRevisionData( revisionData );
		}
	}
}

class UsersInit {
	constructor( editor ) {
		this.editor = editor;
	}

	static get pluginName() {
		return 'UsersInit';
	}

	static get requires() {
		return [ 'Users' ];
	}

	init() {
		const users = this.editor.plugins.get( 'Users' );

		users.addUser( appData.user );
		users.defineMe( appData.userId );
	}
}

let consoleUpdates = 0;
const consoleEl = document.querySelector( '#revision-integration-console' );
const saveButtonEl = document.querySelector( '#revision-integration-save' );

saveButtonEl.addEventListener( 'click', async () => {
	const latestData = {};

	if ( editor ) {
		await editor.plugins.get( 'RevisionTracker' ).update();

		latestData.revisions = editor.plugins.get( 'RevisionHistory' ).getRevisions();
		latestData.editorData = editor.getData();

		updateConsole( latestData );
	}
} );

function updateConsole( data ) {
	const dataEl = document.createElement( 'div' );
	const suggestionsEl = document.createElement( 'div' );

	consoleUpdates++;
	consoleEl.classList.add( 'updated' );

	dataEl.className = 'revision-integration-console__data';
	dataEl.textContent = data.editorData;

	suggestionsEl.className = 'revision-integration-console__suggestions';
	suggestionsEl.textContent = JSON.stringify( data.revisions, undefined, 2 );

	consoleEl.innerHTML = '';
	consoleEl.appendChild( suggestionsEl );
	consoleEl.appendChild( dataEl );

	setTimeout( () => {
		if ( --consoleUpdates == 0 ) {
			consoleEl.classList.remove( 'updated' );
		}
	}, 500 );
}

